.trabajar-en-alertas {
    background: url("/images/background-blue-dark.webp");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    display: flex;
    justify-content: center;
    align-items: center;

    text-align: center;

    padding: 4rem 0;

    color: $white;

    img {
        display: block;

        margin-right: auto;
        margin-left: auto;
        margin-bottom: 2rem;
    }

    h3,
    p {
        color: $white;
    }

    h3 {
        margin-bottom: 1rem;
    }

    p {
        margin-bottom: 2rem;
    }
}
